body,
html {
  margin: 0;
  padding: 0;
}

a{
  text-decoration: none;
}

#container{
  position: absolute;
}

/* Button Panel */
#buttonContainer {
  position: absolute;
  left: 0;
  right: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: flex-end;
  -ms-flex-align: flex-end;
  align-items: flex-end;
  -ms-flex-line-pack: center;
  /* align-content: center; */
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  min-height: 100vh;
}

.button {
  -webkit-box-flex: 1;
    -ms-flex: 1 1 auto;
    flex: 1 1 auto;
    margin: 10px;
    padding: 10px;
    text-align: center;
    position: relative;
    overflow: hidden;
    -webkit-transition: .3s;
    transition: .3s;
    color: #c5b7c3;
}

.button:hover {
  cursor: pointer;
  color: #dbdbdb;
}